/* FONT SETTINGS */
.title-font{
    font-family: 'Poppins', sans-serif;
    font-family: 'Share Tech Mono', monospace;
}
.mid-font{
    font-family: 'Poppins', sans-serif;
    font-family: 'Raleway', sans-serif;
}
/* GENERAL SETTING */
body{
    padding: 1pc;
}
.image-default-set{
    height: 325px;
}

.jkshkil{
    display: flex;
    align-items: center;
}
.image-set{
    border-radius: 5%;
    height: 100%;
    width: 90%;
    opacity: 0.5;
}
.grid-set{
    display: grid;
    align-items: center;
    grid-template-columns: 33% 33% 33%;
    /* height: 25pc; */
}
.grid-set-inner{
    display: grid;
    grid-template-rows: 50% 50%;
}
.quote-set{
    font-family: 'Poppins', sans-serif;
    font-family: 'Share Tech Mono', monospace;
    max-height: 50%;
    text-align: center;
    margin: 0;
    overflow: auto;
}
.btn{
    margin: 0 35%;
}
.backdrop-filter{
    width: 35%;
    backdrop-filter: blur(15px);
    position: relative;
    bottom: 5pc;
    height: 22%;
    margin: 0;
}
::-webkit-scrollbar{
    width: 5px;
}
::-webkit-scrollbar-thumb {
    background: rgb(50, 45, 45); 
    border-radius: 10px;
}

/* QUOTE OF THE DAY SETTINGS */
.q-o-d{
    font-size: initial;
}

/* SECTION SETTING */
section{
    margin: 5pc;
}
/* SELECTORS */
h3{
    text-align: center;
}

/* FOOTER */ 
footer{
    margin: 3pc;
}
footer p{
    text-align: center;
}
footer p::after{
    content: " ðŸ¤–";
}
